import React from 'react';
import Layout from './components/layout';
import styles from './scss/app.scss';
import fetch from 'isomorphic-unfetch';
import PodcastCard from './components/podcastCard';

class Podcasts extends React.Component {
  constructor(props) {
    super(props);
    this.searchBar = React.createRef();
    // Load poecasts into local state, necessary for search bar functionality
    this.state = { podcasts: props.podcasts };
  }
  search(e) {
    const searchBar = console.log(e);
  }
  render() {
    return (
      <Layout>
        <div className={styles.podcast_header}>
          <h1>PODCAST</h1>
        </div>
        <div className={styles.search_bar}>
          <input
            ref={this.searchBar}
            placeholder='Search podcasts'
            onChange={this.search(input)}
          />
        </div>
        <div className={styles.podcast_cards}>
          {this.state.podcasts.map(podcast => {
            return (
              <PodcastCard
                key={podcast.slug}
                title={podcast.acf.podcast_title}
                date={podcast.acf.podcast_date}
                slug={podcast.slug}
              />
            );
          })}
        </div>
      </Layout>
    );
  }
}

Podcasts.getInitialProps = async function() {
  // Get ALL podcasts
  const podcasts = await fetch(
    'http://localhost/whalescout/wp-json/wp/v2/posts?categories=3'
  ).then(podcasts => podcasts.json());

  // Store podcasts into 'props'
  return { podcasts };
};

export default Podcasts;
